.weather-container{
    height: 100%;
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

h1{
    color: #3d3d46;
}

form {
    margin: 20px 20px 0 20px;
    max-width: 500px; /* Maximum width of the form */
    width: 100%;
    display: flex;
    justify-content: center;
}

input{
    width: 100%;
    padding: 15px; 
    border-radius: 15px;
}

input:focus {
    outline: none; /* Remove the default focus outline */
    box-shadow: none; /* Remove any default box shadow */
  }

.content{
    width: 600px;
    height: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 20px;
    background-color: rgba(255, 255, 255, 0.7); /* White background with transparency */
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3); /* Shadow effect */
    border-radius: 10px; /* Optional: for rounded corners */
    padding: 40px; /* Optional: for inner spacing */
    backdrop-filter: blur(10px);
}

.climate{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 20px 0;
}


.image{
    padding: 20px;
}

.description{
    padding: 20px;
}

.other{
    display: flex;
    gap: 40px;
    color: #48484a;
}

@media (max-width: 768px) {

    form{
       max-width: 300px;
    }
    

    .content{
        width: auto;
    }

   .climate{
    flex-direction: column;
   }
}
